import React from 'react';
import { Link } from 'react-router-dom';
import '../styles/DealersPage.css';

const DealersPage = () => {
    return (
        <>
            <div className="section_center">
                <div className="title">
                    <span>BELASCO AUTO</span>
                </div>
                <div className="title_underline"></div>
                <div className="section_images">
                    <div className="image_center">
                        <img src="/World-of-Dealerships-WIP/images/dealers/belasco_auto.png" alt="" />
                    </div>
                </div>
                <div className="section_text">
                    <span><b>Belasco Auto</b> is a dealer that offers two of the largest brands. German precision and luxury from <b>ETK</b> and durability and power from the American company <b>Gavril</b>. This makes Belasco Auto one of the largest dealerships selling the largest number of cars annually. The name of the dealer comes from the city where the company had its first headquarters.</span>
                    <Link style={{ textDecoration: 'none' }} to="/dealer/Belasco_Auto"><p><button>SEE DEALER'S OFFER</button></p></Link>
                </div>
            </div>
            <div className="section_center">
                <div className="title">
                    <span>RICH'S MOTOR COMPANY</span>
                </div>
                <div className="title_underline"></div>
                <div className="section_images">
                    <div className="image_center">
                        <img src="/World-of-Dealerships-WIP/images/dealers/rich's_motor_company.png" alt="" />
                    </div>
                </div>
                <div className="section_text">
                    <span><b>Rich's Motor Company</b> is a dealer with prestige. It has been delivering some of the best cars for many years. The dealer offers cars from <b>Bruckell, Hirochi and Soliad</b>. American classics together with Japanese technological thought give the combination that makes Rich's Motor Company a prestigious dealer.</span>
                    <Link style={{ textDecoration: 'none' }} to="/dealer/Richs_Motor_Company"><p><button>SEE DEALER'S OFFER</button></p></Link>
                </div>
            </div>
            <div className="section_center">
                <div className="title">
                    <span>CLAUDIO MOTORS</span>
                </div>
                <div className="title_underline"></div>
                <div className="section_images">
                    <div className="image_center">
                        <img src="/World-of-Dealerships-WIP/images/dealers/claudio_motors.png" alt="" />
                    </div>
                </div>
                <div className="section_text">
                    <span><b>Claudio Motors</b> appreciates the high class of the products sold. Its offer includes such brands as: <b>Autobello, Civetta, Cherrier and Ibishu</b>. This makes the range of vehicles very large and client can find classic, sports and off-road cars there. The dealer's head office is in Italy.</span>
                    <Link style={{ textDecoration: 'none' }} to="/dealer/Claudio_Motors"><p><button>SEE DEALER'S OFFER</button></p></Link>
                </div>
            </div>
        </>
    );
}

export default DealersPage;