import React from 'react';
import '../styles/Contact.css'

const Contact = () => {
    return (
        <div className="section_center">
            <div className="title">
                <span>CONTACT ME</span>
            </div>
            <div className="title_underline"></div>
            <div className="section_text" style={{ textAlign: 'center' }}>
                Hello there ! :) You can get in touch with me using options below. I will be happy to answer all your questions and it will also be nice to find out about errors on the website. Feel free to contact me! :)
            </div>
            <div className="contact-social">
                {window.innerWidth > 653 && (
                    <>
                        <a href="https://www.instagram.com/p_stoklosa/">
                            <i class="fab fa-instagram-square fa-5x"></i>
                        </a>
                        <a href="https://twitter.com/Piotr67194611">
                            <i class="fab fa-twitter-square fa-5x"></i>
                        </a>
                        <a href="https://github.com/p-k-stoklosa">
                            <i class="fab fa-github-square fa-5x"></i>
                        </a>
                        <a href="mailto:p.k.stoklosa@gmail.com">
                            <i class="fas fa-envelope-square fa-5x"></i>
                        </a>
                    </>
                )}
                {window.innerWidth < 653 && (
                    <>
                        <a href="https://www.instagram.com/p_stoklosa/">
                            <i class="fab fa-instagram-square fa-2x"></i>
                        </a>
                        <a href="https://twitter.com/Piotr67194611">
                            <i class="fab fa-twitter-square fa-2x"></i>
                        </a>
                        <a href="https://github.com/p-k-stoklosa">
                            <i class="fab fa-github-square fa-2x"></i>
                        </a>
                        <a href="mailto:p.k.stoklosa@gmail.com">
                            <i class="fas fa-envelope-square fa-2x"></i>
                        </a>
                    </>
                )}
            </div>
            <div class='contact-logo'>
                <img src="/World-of-Dealerships-WIP/images/logo.png" alt="logo" />
            </div>
        </div>
    );
}

export default Contact;