import React from 'react';
import '../styles/Header.css';
import { Link } from 'react-router-dom';

const handleMobileMenu = (e) => {
    if (e.target.className === "menu") {
        document.querySelector(".mobile_menu").style.width = "100%";
        document.body.style.overflow = "hidden";
    } else {
        document.querySelector(".mobile_menu").style.width = "0%";
        document.body.style.overflow = "auto";
    }
}


const Header = () => {
    return (
        <>
            <div className="logo">
                <img src="/World-of-Dealerships-WIP/images/logo.png" alt="logo" />
            </div>
            {window.innerWidth < 653 && (
                <div className="mobile_menu">
                    <div className="close" onClick={handleMobileMenu}>
                        <i className="fas fa-times fa-2x"></i>
                    </div>
                    <div className="list">
                        <ul>
                            <li className="box">
                                <Link to="/" style={{ textDecoration: 'none', color: 'rgb(150, 150, 150)' }} onClick={handleMobileMenu}>HOME</Link>
                            </li>
                            <li className="box">
                                <span>DEALERS</span>
                                <p>
                                    <Link to="/dealer/Belasco_Auto" style={{ textDecoration: 'none', color: 'rgb(150, 150, 150)' }} onClick={handleMobileMenu}>- Belasco Auto</Link>
                                </p>
                                <p>
                                    <Link to="/dealer/Richs_Motor_Company" style={{ textDecoration: 'none', color: 'rgb(150, 150, 150)' }} onClick={handleMobileMenu}>- Rich's Motor Company</Link>
                                </p>
                                <p>
                                    <Link to="/dealer/Claudio_Motors" style={{ textDecoration: 'none', color: 'rgb(150, 150, 150)' }} onClick={handleMobileMenu}>- Claudio Motors</Link>
                                </p>
                            </li>
                            <li className="box">
                                <a href="https://store.steampowered.com/app/284160/" style={{ textDecoration: 'none', color: 'rgb(150, 150, 150)' }}>
                                    ABOUT GAME
                                </a>
                            </li>
                            <li className="box">
                                ABOUT ME
                            </li>
                            <li className="box">
                                <Link to="/contact" style={{ textDecoration: 'none', color: 'rgb(150, 150, 150)' }} onClick={handleMobileMenu}>CONTACT ME</Link>
                            </li>
                        </ul>
                    </div>
                </div>
            )
            }
            <div className="nav_bar">
                <ul>
                    <li className="menu" onClick={handleMobileMenu}>
                        <i className="fas fa-bars"></i> MENU
                    </li>
                    <li className="box">
                        <Link to="/" style={{ textDecoration: 'none', color: 'rgb(192, 194, 194)' }}>
                            <i className="fas fa-home"></i>
                        </Link>
                    </li>
                    <li className="box">
                        <a href="https://store.steampowered.com/app/284160/" style={{ textDecoration: 'none', color: 'rgb(192, 194, 194)' }}>
                            ABOUT GAME
                        </a>
                    </li>
                    <li className="box">
                        ABOUT ME
                    </li>
                    <li className="box">
                        <Link to="/contact" style={{ textDecoration: 'none', color: 'rgb(192, 194, 194)' }}>CONTACT</Link>
                    </li>
                </ul>
            </div>
        </>
    );
}

export default Header;