import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import '../styles/DealersOffer.css'

class DealersOffer extends Component {
    state = {
        isLoading: true,
        dealerData: [],
        id: null
    }
    findMyIndex = () => {
        return this.state.dealerData.map(el => el.dealer_name).indexOf(this.props.match.params.company);
    }
    componentDidMount() {
        fetch("/World-of-Dealerships-WIP/offer.json")
            .then(response => response.json())
            .then(data => {
                this.setState({
                    dealerData: data,
                    isLoading: false,
                })
            })
    }
    render() {
        const { match } = this.props;
        const { company } = match.params
        const { dealerData, isLoading } = this.state;
        return (
            <>
                {!isLoading && (
                    <div className="dealer_image">
                        <img src={"/World-of-Dealerships-WIP" + dealerData[this.findMyIndex(company)].dealer_image} alt="" />
                    </div>
                )}
                {!isLoading && (
                    dealerData[this.findMyIndex(company)].dealer_offers.map((offer, index) => (
                        <div className="car">
                            <div className="car_image">
                                <img src={"/World-of-Dealerships-WIP" + dealerData[this.findMyIndex(company)].dealer_offers[index].image} alt="" />
                            </div>
                            <div className="car_text">
                                <span className='title'>
                                    {dealerData[this.findMyIndex(company)].dealer_offers[index].title}
                                </span>
                                <span>{dealerData[this.findMyIndex(company)].dealer_offers[index].text}</span>
                            </div>
                            <div className="preview">
                                <Link style={{ textDecoration: 'none' }} to={dealerData[this.findMyIndex(company)].dealer_offers[index].url}>
                                    <button>PREVIEW CAR</button>
                                </Link>
                            </div>
                        </div>
                    ))
                )}
            </>
        );
    }
}

export default DealersOffer;