import React, { Component } from 'react';
import CarContent from '../components/CarContent';
import '../styles/CarPage.css'

class CarPage extends Component {
    state = {
        isLoading: true,
        carData: [],
        id: null
    }
    findMyIndex = () => {
        return this.state.carData.map(el => el.brand).indexOf(this.props.brand);
    }
    componentDidMount() {
        fetch("/World-of-Dealerships-WIP/data.json")
            .then(response => response.json())
            .then(data => {
                this.setState({
                    carData: data,
                    isLoading: false,
                })
            })

    }
    render() {
        const { brand } = this.props;
        const { carData, isLoading } = this.state;
        return (
            <>
                {!isLoading && (
                    <div className="main_image">
                        <img src={"/World-of-Dealerships-WIP" + carData[this.findMyIndex(brand)].main_image} alt="" />
                    </div>
                )}
                {!isLoading && (
                    carData[this.findMyIndex(brand)].sections.map((section, index) => (
                        <CarContent data={carData} id={carData[this.findMyIndex(brand)].id} section_id={carData[this.findMyIndex(brand)].sections[index].id} />
                    ))
                )}
            </>
        );
    }
}

export default CarPage;