import React from 'react';
import '../styles/CarPage.css';

const CarContent = (props) => {
    const { id, data, section_id } = props;
    const section = data[id].sections[section_id];

    const keywords = section.keywords.map(keyword => (
        <p><i>{keyword}</i></p>
    ))
    return (
        <div className={section.section_type}>
            <div className="title">
                <span>{section.section_title}</span>
            </div>
            <div className="title_underline"></div>
            <div className="section_text">
                {section.section_text}
            </div>
            <div className="section_images">
                {section.image_left && (
                    <div className="image_left">
                        <img src={"/World-of-Dealerships-WIP" + section.image_left} alt="" />
                    </div>
                )}
                {section.keywords.length !== 0 && (
                    <div className="keywords">
                        {keywords}
                    </div>
                )}
                {section.image_center && (
                    <div className="image_center">
                        <img src={"/World-of-Dealerships-WIP" + section.image_center} alt="" />
                    </div>
                )}
                {section.image_right && (
                    <div className="image_right">
                        <img src={"/World-of-Dealerships-WIP" + section.image_right} alt="" />
                    </div>
                )}
            </div>
        </div>
    );
}

export default CarContent;