import React from 'react';
import '../styles/Footer.css';
import { Link } from 'react-router-dom';

const Footer = () => {
    return (
        <>
            <div className="important_links">
                {window.innerWidth > 653 && (
                    <>
                        <Link to="/" style={{ textDecoration: 'none' }}>Home</Link>
                        <a href="https://store.steampowered.com/app/284160/" style={{ textDecoration: 'none' }}>About Game</a>
                        <a href="./">About Me</a>
                        <Link to="/contact" style={{ textDecoration: 'none' }}>Contact Me</Link>
                        <a href="./"><i className="fas fa-arrow-up"></i></a>
                    </>
                )}
            </div>
            <div className="socials">
                <a href="https://www.instagram.com/p_stoklosa/">
                    <img src="/World-of-Dealerships-WIP/images/social/instagram.png" alt="instagram" />
                </a>
                <a href="https://twitter.com/Piotr67194611">
                    <img src="/World-of-Dealerships-WIP/images/social/twitter.png" alt="twitter" />
                </a>
                <a href="https://store.steampowered.com/app/284160">
                    <img src="/World-of-Dealerships-WIP/images/social/steam.png" alt="steam" />
                </a>
                <a href="https://github.com/p-k-stoklosa">
                    <img src="/World-of-Dealerships-WIP/images/social/github.png" alt="github" />
                </a>
                <a href="./">
                    <img src="/World-of-Dealerships-WIP/images/social/email.png" alt="email" />
                </a>
            </div>
        </>
    );
}

export default Footer;